






































import Vue from 'vue';

export default Vue.extend({
    created() {
        console.log('디테일', this.router, this.id);
    },
    props: ['id'],
    data() {
        return {
            router: this.$router,
            imgPath: `../../assets/${this.id}.jpg`
        }
    },

    methods: {
        listen(): void {
            alert('파일을 줘야지');
        },
        record(): void {
            alert('뭘눌러봐 눌르긴');
        }
    }
})
    

